import React from 'react';
import { useHistory } from 'react-router';
import { useDispatch, useSelector } from 'react-redux';
import { initialOneData } from "../../../slices/history";
import { makeStyles } from '@material-ui/core/styles';

import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import { TextField } from '@material-ui/core';
import { Typography } from '@material-ui/core';

import { Radio, RadioGroup } from '@material-ui/core';
import { FormControl, FormControlLabel } from '@material-ui/core';
import { Dialog, DialogActions, DialogTitle } from '@material-ui/core';

import {firebaseDb} from '../../../plugins/firebase';

let keyIn = 0;
let data = {};

const useStyles = makeStyles((theme) => ({
  root:{
    textAlign: 'center',
    fontSize: 18,
  },
  check1:{
    color: '#ff0000'
  },
  haikei :{
    margin: 'auto',
    //textAlign:'right',
    width: 320,
    height: 45,
  },
  Rbutton: {
    width: '100px',
    height: '50px',
    fontWeight: 'bold',
    color: '#FFF',
    background: '#5cca13',
    margin: '40px',
    '&:hover': {
      background: ' #4eaa10',
    },
  },
  radio: {
    marginLeft: theme.spacing(2),
  },

  size :{
    width: 320,
    backgroundColor: theme.palette.background.paper,
  },
  size1 :{
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(2),
    width: 135,
  },
}));

const TrainingComponent = () => {
  const classes = useStyles();
  const history = useHistory();
  const dispatch = useDispatch();
  const userId = useSelector(state => state.auth.user.userId);
  const name = useSelector(state => state.auth.user.userName);
  const flagT = useSelector(state => state.auth.flagT);
  const oneData = useSelector(state => state.history.oneData);
  const user = useSelector(state => state.history.user);
  const [flag, setFlag] = React.useState(true);
  // 
  if(flag){
    keyIn = 0;
    let data2 = {};
    data2.company = ""; data2.location = "";
    data2.facility = "";  data2.address = "";
    data2.training_start = "";  data2.training_end = "";
    data2.time_start = ""; data2.time_end = "";
    data2.detail = ""; data2.impressions = "";
    data = data2;
    setFlag(false);
  };
  if(oneData !== null) {
    keyIn = 1;
    data = oneData;
    dispatch(initialOneData());
  };
  const [values, setValues] = React.useState({
    company: data.company, location: data.location,
    facility: data.facility, address: data.address,
    training_start: data.training_start, training_end: data.training_end,
    time_start: data.time_start, time_end: data.time_end,
    detail: data.detail, impressions: data.impressions, approval: data.approval,
  });
  const [open, setOpen] = React.useState(false);

  const [check1, setCheck1] = React.useState("");
  const [check2, setCheck2] = React.useState("");
  const [check3, setCheck3] = React.useState("");
  const [check4, setCheck4] = React.useState("");
  const [check5, setCheck5] = React.useState("");
  const [check6, setCheck6] = React.useState("");
  const [check7, setCheck7] = React.useState("");
  const [check8, setCheck8] = React.useState("");

  const handleChange = (prop) => (event) => {
    setValues({ ...values, [prop]: event.target.value });

    if(event.target.value.length > 0){
      if(prop === 'company'){
        setCheck1("");
      }
      if(prop === 'location'){
        setCheck2("");
      }
      if(prop === 'facility'){
        setCheck3("");
      }
      if(prop === 'address'){
        setCheck4("");
      }
      if(prop === 'training_start'){
        setCheck5("");
      }
      if(prop === 'training_end'){
        setCheck5("");
      }
      if(prop === 'time_start'){
        setCheck6("");
      }
      if(prop === 'values.time_end'){
        setCheck6("");
      }
      if(prop === 'detail'){
        setCheck7("");
      }
      if(prop === 'impressions'){
        setCheck8("");
      }
    }
  };

  const handleClickOpen = () => {
    let cnt = 0;

    if(values.company === "") {
      setCheck1("");
      cnt += 1;
    }
    if(values.location === "") {
      setCheck2("");
      cnt += 1;
    }
    if(values.location === "") {
      if(values.facility === "") {
        setCheck3("");
        cnt += 1;
      }
    }
    if(values.address === "") {
      setCheck4("");
      cnt += 1;
    }
    if(values.training_start === "" || values.training_end === "") {
      setCheck5("");
      cnt += 1;
    }
    if(values.training_start === values.training_end) {
      if(values.time_start === "" || values.time_end === "") {
        setCheck6("");
        cnt += 1;
      }
    }
    if(values.detail === "") {
      setCheck7("");
      cnt += 1;
    }
    if(values.impressions === "") {
      setCheck8("");
      cnt += 1;
    }
    
    if(cnt === 0) {
      setOpen(true);
    }
  };
  const handleClose = () => {
    setOpen(false);
  };

  const formatText1 = () => {
    if(flagT === false) {
      return(
        <div>
          <Button 
            className={classes.Rbutton}
            variant="contained" color="primary"
            disabled={values.approval} onClick={handleClickOpen}
          >
            
          </Button>

          <Dialog
            open={open}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
          >
          <DialogTitle id="alert-dialog-title">{""}</DialogTitle>
          <DialogActions>
            <Button onClick={handleOnClickI} color="primary">
              
            </Button>
            <Button onClick={handleClose} color="primary">
              
            </Button>
          </DialogActions>
          </Dialog>
        </div>
      );
    } else {
      return(
        <div>
          <Button 
            className={classes.Rbutton}
            variant="contained" color="primary"
            disabled={values.approval} onClick={handleClickOpen}
          >
            
          </Button>

          <Dialog
            open={open}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
          >
          <DialogTitle id="alert-dialog-title">{""}</DialogTitle>
          <DialogActions>
            <Button onClick={handleOnClickI} color="primary">
              
            </Button>
            <Button onClick={handleClose} color="primary">
              
            </Button>
          </DialogActions>
          </Dialog>
        </div>
      );
    };
  };

  const time = new Date().getTime();
  const day = new Date().toLocaleString();
  const today = time * -1;
  today.toLocaleString();

  let approval = false;

  const handleOnClickI = () => {
    let id = userId;

    if(flagT) {
      id = user.user;
      approval = true;
    };
    const Ref = firebaseDb.ref(id +'r/training');
    const ref = firebaseDb.ref('report/training/' + id);
    if(keyIn === 0) {
      const DayRef = Ref.child(time);
      DayRef.set({
        "company" : values.company,
        "location" : values.location,
        "facility" : values.facility,
        "address" : values.address,
        "training_start" : values.training_start,
        "training_end" : values.training_end,
        "time_start" : values.time_start,
        "time_end" : values.time_end,
        "detail" : values.detail,
        "impressions" : values.impressions,
        "today" : today,
        "day" : day,
        "approval" : approval,
      });
      const dayRef = ref.child(time);
      dayRef.set({"company" : values.company});
      ref.update({"name" : name});
    }
    else {
      keyIn = 0;
      const updateRef = Ref.child(data.key);
      const updRef = ref.child(data.key);
      updateRef.set({
        "company" : values.company,
        "location" : values.location,
        "facility" : values.facility,
        "address" : values.address,
        "training_start" : values.training_start,
        "training_end" : values.training_end,
        "time_start" : values.time_start,
        "time_end" : values.time_end,
        "detail" : values.detail,
        "impressions" : values.impressions,
        "today" : today,
        "day" : day,
        "approval" : approval,
      });
      updRef.set({"company" : values.company});
      if(flagT) {
        updateRef.update({"approver" : name});
      };
    };

    if(flagT) {
      alert('');
      history.push('/home2/book');
    }
    else {
      alert('\n');
      history.push('/home/select/report');
    };
  };

  return (
    <div className={classes.root}>
      <div className="centerTable">
        <p></p>
          <Typography className={classes.check1} >{check1}</Typography>
          <TextField
            className={classes.size}
            name='company' label="" 
            variant="outlined" disabled={flagT}
            value={values.company} onChange={handleChange('company')}
          />

        <br/>
        <p></p>
          <Typography className={classes.check1} >{check2}</Typography>
          <Paper variant="outlined" className={classes.haikei}>
            <FormControl className={classes.radio}>
              <RadioGroup rowaria-label="gender" name="gender1" value={values.location} onChange={handleChange('location')}>
                <FormControlLabel value="" disabled={flagT} control={<Radio />} label="" />
                <FormControlLabel value="" disabled={flagT} control={<Radio />} label="" />
                <FormControlLabel value="" disabled={flagT} control={<Radio/>} label="" />
              </RadioGroup>
            </FormControl>
          </Paper>

        <p></p>
          <Typography className={classes.check1} >{check3}</Typography>
          <TextField
            className={classes.size}
            name='facility' label="" 
            variant="outlined" disabled={flagT}
            value={values.facility} onChange={handleChange('facility')}
          />

        <br/><br/>
          <Typography className={classes.check1} >{check4}</Typography>
          <TextField
            className={classes.size}
            name='address' label="" 
            variant="outlined" disabled={flagT}
            value={values.address} onChange={handleChange('address')}
          />

        <p></p>
          <Typography className={classes.check1} >{check5}</Typography>
          <TextField
            className={classes.size1}
            name='training_start'
            type="date" disabled={flagT}
            value={values.training_start}
            onChange={handleChange('training_start')}
          />
          
          <TextField
            className={classes.size1}
            name='training_end'
            type="date" disabled={flagT}
            value={values.training_end}
            onChange={handleChange('training_end')}
          />

        <p></p>
          <Typography className={classes.check1} >{check6}</Typography>
          <TextField
            className={classes.size1}
            name='time_start'
            type="time" disabled={flagT}
            value={values.time_start}
            onChange={handleChange('time_start')}
          />
          
          <TextField
            className={classes.size1}
            name='time_end'
            type="time" disabled={flagT}
            value={values.time_end}
            onChange={handleChange('time_end')}
          />

        <p></p>
          <Typography className={classes.check1} >{check7}</Typography>
          <TextField
            className={classes.size}
            name='detail' label="" 
            variant="outlined" disabled={flagT}
            multiline rowsMax={4}
            value={values.detail}
            onChange={handleChange('detail')}
          />

        <p></p>
          <Typography className={classes.check1} >{check8}</Typography>
          <TextField
            className={classes.size}
            name='impressions' label="" 
            variant="outlined" disabled={flagT}
            multiline rowsMax={4}
            value={values.impressions}
            onChange={handleChange('impressions')}
          />
        <br></br>

        {formatText1()}

      </div>
    </div>
  );
};

export default TrainingComponent;
