import React, { useEffect, useRef } from 'react';
import { useLocation } from 'react-router-dom';

import Header from '../../../components/header/Header3Component';
import History from '../../../components/report/rHistory/History';
import NotFound from '../../NotFound';

const useQuery = () => {
  // useLocationlocation
  const location = useLocation();

  // location
  return new URLSearchParams(location.search);
};

const HistoryContainer = () => {
  const messageEndRef = useRef();
  const scrollToLatest = () => {
    messageEndRef.current.scrollIntoView({
      block: 'center',
    });
  };

  useEffect(() => {
    console.log('HistoryContainer:useEffect');
    scrollToLatest();
  }, []);

  // 
  const query = useQuery();
  const targetPage = query.get('page');

  let title = "";
  switch(targetPage) {
    case '1':
      title="";
      break;
    case '2':
      title="";
      break;
    case '3':
      title="";
      break;
    case '4':
      title="";
      break;
    case '5':
      title="";
      break;
    case '6':
      title="";
      break;
    case '7':
      title="";
      break;
    default:
      return (
        <div>
          <NotFound />
        </div>
      );
  };
  return (
    <div>
      <Header title={title} />
      <div id="top-of-list" ref={messageEndRef} />
      <History targetPage={targetPage} />
    </div>
  );
};

export default HistoryContainer;
